import { PAGE_QUERYResult } from "@/sanity/types";
import Image from "next/image";
import { urlFor } from "@/sanity/lib/image";

type FeaturesProps = Extract<
    NonNullable<NonNullable<PAGE_QUERYResult>["content"]>[number],
    { _type: "features" }
>;

export function Features({ features, title }: FeaturesProps) {
    return (
        <div className="wp-block-multidots-multipurpose-block is-block-center mdhome-why-multidots mdinc-section-bottom-margin mdcareer-perks">
            {title ? (
                <h2
                    className="wp-block-heading has-text-align-center has-text-color"
                    id="mdcareer-perks-and-benefits"
                    style={{ color: "#000000" }}
                >                            {title}
                </h2>
            ) : null}

            <div className="wp-block-multidots-multipurpose-block is-block-center mdhome-why-multidots-boxes">
                <div className="wp-block-columns mdhome-why-multidots-columns ">
                    {Array.isArray(features) ? (
                        features.map((feature, index) => {
                            const isLastInColumn =
                                index === Math.ceil(features.length / 2) - 1 || index === features.length - 1;

                            return (
                                <div
                                    key={feature._key}
                                    className="wp-block-column mdhome-why-multidots-column is-layout-flow wp-block-column-is-layout-flow"
                                    style={{ flexBasis: "50%" }}
                                >
                                    <div
                                        className={`flex flex-row gap-12 p-4 ${!isLastInColumn ? 'border-b border-gray-200' : ''}`}
                                    >
                                        <div className="flex items-center justify-center relative">
                                            <div
                                                className="w-15 h-15 absolute rounded-full z-0 translate-x-2 left-2"
                                                style={{
                                                    backgroundColor: feature.backgroundColor?.hex || '#ffffff',
                                                }}
                                            ></div>

                                            {feature.icon && (
                                                <Image
                                                    src={urlFor(feature.icon).width(60).height(60).url()}
                                                    width={60}
                                                    height={60}
                                                    alt={feature.title}
                                                    className="object-contain relative"
                                                />
                                            )}
                                        </div>

                                        <div className="flex flex-col gap-4">
                                            <h3 className="text-xl font-semibold text-slate-800">{feature.title}</h3>
                                            <p className="text-lg text-slate-600">{feature.text}</p>
                                        </div>
                                    </div>
                                </div>
                            );
                        })
                    ) : null}


                </div>

            </div>
        </div>

    );
}